header{position:fixed;top:0;left:0;display:flex;flex-direction:row;align-items:center;width:100%;justify-content:space-between;background-color:#fff;padding:16px;box-sizing:border-box}header h1{font-size:28px}header .timer{font-size:20px}header .back-button{background-color:transparent;color:#007bff;border:none;cursor:pointer;font-size:24px;transition:color .3s ease;display:flex;gap:10px;align-items:center}header .back-button::before{content:"➤";display:inline-block;transform:rotate(-180deg)}header .back-button:hover{color:#0062cc}body{font-family:"Arial",sans-serif;background-color:#f8f9fa;color:#333;margin:0;padding:0;display:flex;flex-direction:column;align-items:center;justify-content:center;min-height:100vh;text-align:center;position:relative}body::before{content:"";background-image:url("../assets/background.jpg");background-size:cover;position:absolute;top:0;right:0;bottom:0;left:0;opacity:.75}body #root{position:absolute}.container{max-width:80%;padding:20px 30px;border-radius:8px;background-color:#fff;box-shadow:0 0 10px rgba(0,0,0,.2)}.form label{font-size:18px;display:block;margin-bottom:10px}.form input[type=text]{width:90%;padding:12px;border:2px solid #007bff;border-radius:4px;margin-bottom:20px;font-size:18px;background-color:#fff;color:#333}button.primary{background-color:#007bff;color:#fff;padding:12px 24px;border:none;border-radius:4px;cursor:pointer;font-size:18px;transition:background-color .3s ease}button.primary:disabled{background-color:#ccc;cursor:not-allowed}button.primary:hover:not([disabled]){background-color:#0062cc}.checkbox-container{display:flex;align-items:center;margin-bottom:15px}.checkbox-input{margin-right:10px;appearance:none;width:20px;height:20px;border:2px solid #007bff;border-radius:4px;cursor:pointer;position:relative}.checkbox-input:checked{background-color:#007bff;border-color:#007bff}.checkbox-input:before{content:"✓";color:#fff;font-size:16px;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%) scale(0);transition:transform .2s ease}.checkbox-input:checked:before{transform:translate(-50%, -50%) scale(1)}.checkbox-label{font-size:16px;cursor:pointer}.page{display:flex;flex-direction:column;align-items:center}